import React from 'react'
import  {motion} from 'framer-motion'
import im from './1.jpg'
import ali from 'sweetalert2'
import 'bootstrap/dist/css/bootstrap.min.css'
const Moition = () => {
  return (
   
   <div className='border border-dark w-25 m-auto mt-5 ' style={{height:300}}>
    <button onClick={()=> ali.fire({title:"Ali",html:"dsfdsf",focusConfirm:false ,showCancelButton:true,cancelButtonText:"Colse",showDenyButton:true}).then(res=>{ if(res.isConfirmed){ ali.fire('save','','success')}else if(res.isDenied){ali.fire('Do not save','','error')}})}>Click</button>
    <motion.div className='d-inline-block border border-danger' 
 
   > 
    <img src={im} width={100}/>
   </motion.div>
    <p className='d-inline-block'>My Name : Ali Osama Mahdi</p>
   </div>
  )
}

export default Moition